<template>
	<view class="u-flex u-flex-col c-container">
		<image class="c-bg" src="../../static/mybg.png" mode=""></image>
		<view class="c-user u-col-center">
			<view class="c-user-av">
				<image class="c-user-av-image" src="../../static/codelogo.png" mode=""></image>
			</view>
			<view class="c-user-name">
				<text class="">黑水</text>
			</view>
			<view class="c-user-info">
				<view class="c-bar">
					<view class="c-bar-item">
						<text class="c-unimpo">学院</text>
						<text class="c-impo">计算机学院</text>
					</view>
					<view class="c-bar-item">
						<text class="c-unimpo">学院</text>
						<text class="c-impo">软件1935</text>
					</view>
					<view class="c-bar-item">
						<text class="c-unimpo">宿舍</text>
						<text class="c-impo">团结楼607</text>
					</view>
				</view>
				<view class="c-bar">
					<view class="c-bar-item">
						<text class="c-unimpo">专业</text>
						<text class="c-impo">软件技术</text>
					</view>
					<view class="c-bar-item">
						<text class="c-unimpo">学号</text>
						<text class="c-impo">191702228</text>
					</view>
				</view>
			</view>
		</view>
		<view class="c-tools">
			<view v-if="!isLogined" class="c-item" @click="exitLogin()">
				<view class="c-button-box">
					<!-- <u-icon name="file-text"></u-icon> -->
					<text class="c-item-text">退出登录</text>
				</view>
				<view class="">
					<u-icon color="#d76333" name="arrow-right"></u-icon>
				</view>
			</view>
			<view class="c-item" @click="gotoMyFile()">
				<view class="c-button-box">
					<!-- <u-icon name="file-text"></u-icon> -->
					<text class="c-item-text">我的文件</text>
				</view>
				<view class="">
					<u-icon color="#d76333" name="arrow-right"></u-icon>
				</view>
			</view>
			<view class="c-item" @click="gotoActivity()">
				<view class="c-button-box">
					<!-- <u-icon name="file-text"></u-icon> -->
					<text class="c-item-text">我的活动</text>
				</view>
				<view class="">
					<u-icon color="#d76333" name="arrow-right"></u-icon>
				</view>
			</view>
			<view class="c-item" @click="gotoAbout()">
				<view class="c-button-box">
					<!-- <u-icon name="file-text"></u-icon> -->
					<text class="c-item-text">关于我们</text>
				</view>
				<view class="">
					<u-icon color="#d76333" name="arrow-right"></u-icon>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isLogined: '',
			}
		},
		methods: {
			
			gotoActivity(){
				uni.navigateTo({
					url: '../activity/activity',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			gotoMyFile(){
				uni.navigateTo({
					url: '../myFile/myFile',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			gotoAbout(){
				uni.navigateTo({
					url: '../about/about',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			exitLogin() {
				try {
					uni.setStorageSync('isLogined', 'unlogined');
					uni.showToast({
						title: '退出成功！'
					});
					try {
					    const value = uni.getStorageSync('isLogined');
						this.isLogined = value;
					    if (value) {
					        console.log(value);
					    }
					} catch (e) {
					    // error
					}
					console.log("成功退出")
				} catch (e) {
					console.log("出错了")
				}
			}
		}
	}
</script>

